;EACZ;;EAEA,UAAU;;EAEV;IACE,cAAc;EAChB;;EAEA;IACE,cAAc;EAChB;AACF;;AAEA;EACE;IACE,WAAW;IACX,kBAAkB;EACpB;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,2BAA2B;EAC7B;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,UAAU;IACV,kBAAkB;EACpB;AACF","sourcesContent":[":root {\n  background-color: black;\n  --active-color: #111;\n\n  --carrier: red;\n  --battleship: blueviolet;\n  --cruiser: greenyellow;\n  --submarine: gold;\n  --destroyer: green;\n\n  --menu-hover: rgb(49, 49, 49);\n\n  font-family: \"Courier New\", Courier, monospace;\n}\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nnav ul {\n  margin: 0;\n  padding: 0;\n}\n\nnav li {\n  list-style: none;\n}\n\nhtml,\nbody {\n  margin: 0;\n  padding: 0;\n}\n\nbody {\n  position: relative;\n  padding: 0;\n  background-color: black;\n  height: calc(var(--vh) * 100);\n  width: 100%;\n  overflow: hidden;\n}\n\n/* CONTAINER */\n\n#container {\n  display: grid;\n  grid-template-columns: 1fr;\n  grid-template-rows: auto;\n  justify-items: center;\n  width: 100%;\n  height: 100%;\n  background: #111;\n}\n\n/* START MENU */\n\n.menu {\n  grid-area: 1 / 1 / -1 / -1;\n  padding: 1rem 0;\n  align-self: center;\n  color: white;\n  width: 100%;\n}\n\n.menu-form {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n}\n\n.input-div {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n}\n\n.input-div > input {\n  background-color: #181818;\n  border: none;\n  border-bottom: #000 2px solid;\n  color: white;\n  min-width: 20%;\n  transition: 0.2s;\n  margin-bottom: 0.5rem;\n  font-size: 2vh;\n  font-family: inherit;\n}\n\n.input-div input:focus,\n.input-div input:hover {\n  transition: 0.5s;\n  border-bottom: var(--active-color) 2px solid;\n}\n\n.username-two:disabled {\n  transition: 0.2s;\n  opacity: 0;\n  margin: 0;\n  padding: 0;\n  font-size: 0;\n  border-bottom: var(--active-color) 0px solid;\n  filter: blur(1rem);\n}\n\n.input-div > label {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  margin-bottom: 0.5rem;\n  cursor: pointer;\n  font-size: 2vh;\n}\n\n.input-div > label > input {\n  margin-left: auto;\n}\n\n.menu-form > button {\n  width: 100%;\n  background-color: #000;\n  color: white;\n  border: none;\n  margin: 0;\n  padding: 0.5rem 0;\n  transition: 0.25s;\n  font-size: 3vh;\n  cursor: pointer;\n  font-family: inherit;\n}\n\n.menu-form > button:focus,\n.menu-form > button:hover {\n  padding: 0.75rem 0;\n  transition: 0.5s;\n  background-color: var(--menu-hover);\n}\n\n/* SET SHIPS */\n\n.set-ships-wrapper {\n  display: flex;\n  flex-direction: column;\n  height: auto;\n  width: 90%;\n  justify-content: center;\n  user-select: none;\n}\n\n/* SHIPS STORAGE */\n\n.ship-storage {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-around;\n  align-items: center;\n  width: 100%;\n  height: auto;\n  margin: 1rem 0rem;\n}\n\n.ship-storage .horizontal-ships {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: center;\n}\n\n.ship-storage .vertical-ships {\n  display: flex;\n  flex-direction: row;\n  align-items: flex-start;\n  justify-content: flex-start;\n}\n\n.cell-group {\n  display: flex;\n  height: max-content;\n  width: max-content;\n  cursor: move;\n  z-index: 1;\n}\n\n.cell-group.horizontal {\n  flex-direction: row;\n}\n\n.cell-group.vertical {\n  flex-direction: column;\n}\n\n.cell-group:active {\n  z-index: 10;\n}\n\n.cell-group .cell {\n  height: auto;\n  width: 8vw;\n  aspect-ratio: 1;\n  background-color: white;\n  border: solid 1px #000;\n  box-shadow: inset 0 0 0.5vh 0 #0000007a;\n}\n\n/* SET SHIPS GRID */\n\n.place-ships {\n  width: auto;\n  height: auto;\n  aspect-ratio: 1;\n  background-color: #00485c;\n  padding: 0.5%;\n}\n\n.place-ships .hover-carrier,\n.place-ships .hover-battleship,\n.place-ships .hover-cruiser,\n.place-ships .hover-submarine,\n.place-ships .hover-destroyer {\n  -webkit-mask-position: center;\n  -webkit-mask-size: contain;\n  -webkit-mask-repeat: no-repeat;\n  -webkit-mask-image: url(./img/svg/shot.svg), url(./img/svg/hit.svg);\n  mask-position: center;\n  mask-size: contain;\n  mask-repeat: no-repeat;\n  mask-image: url(./img/svg/shot.svg), url(./img/svg/hit.svg);\n}\n\n.place-ships .hover-carrier {\n  background-color: var(--carrier) !important;\n}\n\n.place-ships .hover-battleship {\n  background-color: var(--battleship) !important;\n}\n\n.place-ships .hover-cruiser {\n  background-color: var(--cruiser) !important;\n}\n\n.place-ships .hover-submarine {\n  background-color: var(--submarine) !important;\n}\n\n.place-ships .hover-destroyer {\n  background-color: var(--destroyer) !important;\n}\n\n.set-ship-title {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  font-size: 3vw;\n  color: white;\n  margin: 0;\n}\n\n.buttons {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  width: 100%;\n  gap: 1vh;\n}\n\n.buttons > *:disabled {\n  color: rgba(255, 255, 255, 0.5) !important;\n  background-color: #000 !important;\n}\n\n.randomize-button,\n.continue-button {\n  width: 100%;\n  background-color: #000;\n  color: white;\n  border: none;\n  margin: 0;\n  padding: 0.5vh 0;\n  transition: 0.25s;\n  cursor: pointer;\n  font-family: inherit;\n}\n\n.randomize-button:hover,\n.continue-button:hover {\n  transition: 0.2s;\n  background-color: var(--menu-hover);\n}\n\n.continue-button {\n  font-size: 5vw;\n}\n\n.randomize-button {\n  font-size: 4vw;\n}\n\n/* MODAL */\n\n.modal-wrapper {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: absolute;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  -webkit-backdrop-filter: blur(2rem) grayscale(100%);\n  backdrop-filter: blur(2rem) grayscale(100%);\n  background-color: rgba(0, 0, 0, 0.5);\n  z-index: 1000;\n}\n\n.modal {\n  position: relative;\n 